  padding: 0;
}

.groups ul {
  margin: 0;
  padding: 0;
}

.groups ul li:hover {
  background-color: #F9F9F9;
}

.group {
  padding: 20px 0;
  zoom: 1;
}

.group-picture {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  margin: 0;
  padding: 0;
}

.group-title {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  color: #21252A;
}

.group-new-message {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: linear-gradient(96.13deg, #FEA31F 0%, #E3496B 100%);
  margin-left: 10px;
  margin-top: 10px;
}

.group-last-message {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  color: #868E96;

  width: 100%;
  
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.group-last-message-time-push-to-bottom {
  width: 100%;
  height: 45%;
}

.group-last-message-time {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  color: #21252A;
}

@media screen and (min-width: 600px) {
  .group {
    zoom: 1.35;
  }
}
.image-as-button {
  cursor: pointer;
}

.message-content {
  height: 45vh;
}

.center-on-desktop {
  align-items: initial;
  height: auto;
  height: initial;
}

.name {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 18px;
  color: #868E96;
}

.chat {
  width: 100%;
  height: 50vh;
  border: solid 1px #EEE;
  padding: 10px;
  overflow-y: scroll;
}

.remove-margin {
  margin-top: -7.5px !important;
}

.messages {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}

.message {
  border-radius: 20px;
  padding: 8px 15px;
  margin-top: 5px;
  margin-bottom: 5px;
  display: inline-block;
}

.yours {
  align-items: flex-start;
}

.yours .message {
  margin-right: 25%;
  background-color: #eee;
  position: relative;
}

.yours .message.last:before {
  content: "";
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: -7px;
  height: 20px;
  width: 20px;
  background: #eee;
  border-bottom-right-radius: 15px;
}
.yours .message.last:after {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: -10px;
  width: 10px;
  height: 20px;
  background: white;
  border-bottom-right-radius: 10px;
}

.mine {
  align-items: flex-end;
}

.mine .message {
  color: white;
  margin-left: 25%;
  background: linear-gradient(105.3deg, #FEA31F 0%, #E3496B 100%) !important;
  background-attachment: fixed;
  position: relative;
}

.mine .message.last:before {
  content: "";
  position: absolute;
  z-index: 0;
  bottom: 0;
  right: -8px;
  height: 20px;
  width: 20px;
  background: linear-gradient(105.3deg, #E3496B 100%, #E3496B 0%) !important;
  background-attachment: fixed;
  border-bottom-left-radius: 15px;
}

.mine .message.last:after {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  right: -10px;
  width: 10px;
  height: 20px;
  background: white;
  border-bottom-left-radius: 10px;
}

#custom-scrollbar::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 1px rgba(0,0,0,0);
	background-color: #F5F5F5;
}

#custom-scrollbar::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

#custom-scrollbar::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}

.responsive-card {
  width: 45vw !important;
  margin-bottom: 1vh !important;
}

.locked {
  border-radius: inherit;
  background: rgba(0,0,0,.6);
  border-radius: 0.25em;
}

@media screen and (min-width: 1200px) {
  .center-on-desktop {
    height: 80%;
    align-items: center;
  }
}

@media screen and (max-width: 1200px) {
  .responsive-card {
    width: 90vw !important;
    margin-bottom: 1vh !important;
  }
}

